}

main .aside-article-intro,
main .article-friends,
main .aside-article-photos {
    margin-bottom: 1rem;
}

/*main  aside-section  aside-article-intro"*/

main .aside-article-intro {
    background: white;
    width: 100%;
    box-shadow: 0 0 14px 1px rgba(0, 0, 0, 0.3);
    padding: 1rem;
    border-radius: 5px;
}

main .aside-article-header {
    display: flex;
    justify-content: start;
    align-items: center;
    margin-bottom: 1rem;
}

main .aside-article-header h3 {
    padding: 0 1rem;
}

.aside-intro-content {
    margin-bottom: 0.7rem;
}

main .aside-intro-content h4,
main .aside-intro-content p {
    margin-bottom: 0.3rem;
}

main .aside-intro-content p {
    color: rgb(192, 192, 192);
    font-size: 0.8rem;
}

main .aside-intro-update a {
    text-decoration: none;
    color: rgb(64, 163, 244);
    font-weight: bold;
}

main .aside-article-icon {
    position: relative;
    overflow: hidden;

}

main .aside-article-icon:before {
    display: block;
    content: '';
    width: 35px;
    height: 35px;
    background: rgb(224, 224, 224);
    border-radius: 50%;
}

main .aside-article-icon i {
    background: rgb(224, 224, 224);
    color: white;
    display: block;
    position: absolute;
    /* top: 0;
    left:0; */
    /* width: 100%;
    height: auto; */
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

/*main  aside-section  aside-article-photos*/

main .aside-article-photos,
main .aside-article-friends {
    background: white;
    width: 100%;
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
    padding: 1rem;
    border-radius: 5px;
}

main .aside-article-gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    align-items: center;
}

main ul.aside-article-gallery {
    padding-left: 0;
    padding-bottom: 0;
}



main ul.aside-article-gallery li {
    flex: 0 1 32%;
    /* height: 50px; */
    /* width: 30%; */
    list-style: none outside;
    padding-left: 0;
    margin: 1px 2px 1px 0;

    /* Center images */
    display: flex;
    justify-content: center;
    align-content: center;
    overflow: hidden;
    /* border: 1px solid black; */
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);

    position: relative;
}

main ul.aside-article-gallery li div {
    flex: 0 0 100%;
    position: relative;
    /* margin: 0; */
    /* background: red; */

}

main ul.aside-article-gallery li div:after {
    display: block;
    content: '';
    padding-top: 100%;
}

main ul.aside-article-gallery li a {
    flex: 0 0 100%;
    position: relative;

}

main ul.aside-article-gallery li a:after {
    display: block;
    content: '';
    padding-top: 100%;

}

main ul.aside-article-gallery li img {
   
    width: auto;
    height: 100%;
    background: white;

    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

main ul.aside-article-gallery li img.l {
    width: 100%;
    height: auto;
  
    background: white;

    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.zoom:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}


main ul.aside-article-gallery li div.img-details{
    width: 100%;
    height: 100%;
    position: absolute;
    content: "";
    background:  rgba(64, 163, 244, 0.9);
    opacity: 0;
    transition: all .4s ease-in;
    display:flex;
    justify-content: center;
    align-items: center;
    
}


main ul.aside-article-gallery li div.img-details:hover{
    opacity: 1;
}

